table {
    margin-top: 30px;
    display: inline-table;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 0 10px #c6cccced;
    width: 370px;
    border-spacing: 0;
    margin-top: 20px;
  }
  
  thead tr {
    margin-bottom: 5px;
    padding: 20px;
    border-radius: 6px;
  }
  
  table th {
    font-weight: 800;
    font-size: 22px;
    margin-bottom: 5px;
    color: #f5f507;
    text-shadow: #000 1px 0px 1px, #000 0px 1px 1px, #000 0px -2px 1px, #000 -2px 0px 2px;
    }
  
  
  table td {
    padding: 10px;
    /* width: inherit; */
    font-weight: 700;
    font-size: 18px;
    color: #d3d322;
    display: flex;
    justify-content: center;
    text-shadow: #000 1px 0px 1px, #000 0px 1px 1px, #000 0px -2px 1px, #000 -2px 0px 2px;
   }
  
  table tr {
    display: flex;
    justify-content: space-around;
    border-bottom: solid 1px #c6cccce6;
    border-radius: 6px;
    background-color: #bebaba;
    }
  
  
  table tr:not(:last-child) {
    margin-bottom: 2px;
  }
  
  