.profile {
    width: 1000px;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 15px;
    width: 370px;
    height: 500px;
    box-shadow: 0 0 10px #a4dfdfed;
    border-radius: 10px;
margin-top: 20px;
  }
  
  .description {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: inherit;
    border-bottom: #a4dfdfed solid 2px;
  }
  
  .avatar {
    width: 300px;
    box-shadow: 0 0 3px 0px #0000004d;
    border-radius: 50%;
    background-color: #a4dfdfed;
    padding: 20px 10px 0px 10px;
    margin-bottom: 15px;
  }
  
  .name {
    font-weight: 700;
    font-size: 30px;
    margin-bottom: 15px;
    color: #208585ed;
  }
  
  .tag {
    font-weight: 500;
    font-size: 20px;
    color: #208585ed;
    margin-bottom: 15px;
  }
  
  .location {
    font-weight: 700;
    font-size: 20px;
    color: #208585ed;
    margin-bottom: 15px;
  }
  .stats {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin: auto;
    padding: 0;
    width: inherit;
    background-color: #208585ed;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
  }
  
  .stats > li {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 5px;
    min-width: 80px;
    padding: 7px 0;
  }
  
  .label {
    margin-right: 3px;
    font-weight: 700;
    font-size: 20px;
    color: #75d3d3ed;
    margin-bottom: 5px;
  }
  
  .quantity {
    font-weight: 800;
    font-size: 20px;
    color: #75d3d3ed;
  }